<template>
    <div class="index-home">
        <van-search
            shape="round"
            background="#4fc08d"
            disabled
            placeholder="请输入搜索关键词"
            @click.stop="handleSearchTab"
        />
        <!-- 选项卡 -->
        <van-tabs
            v-model="active1"
            swipeable
            animated
            background="#4fc08d"
            :border="false"
            @change="setTabData"
            line-width="16px"
            color="#fff"
            title-active-color="#fff"
        >
            <van-tab
                :title="item.title"
                :name="item.name"
                v-for="(item, index) in $store.state.tab.indexTab"
                :key="index"
                :title-style="{ fontWeight: 'bold', fontSize: '16px' }"
            >
                <div
                    @scroll="scroll"
                    :ref="item.name"
                    :class="['swiper-item', item.name]"
                >
                    <!-- 轮播图 -->
                    <template v-if="index === 0">
                        <div class="index-swiper-container">
                            <van-swipe
                                :autoplay="3000"
                                class="my-swipe"
                                indicator-color="#4fc08d"
                            >
                                <van-swipe-item
                                    v-for="(item, index) in $store.state.blog
                                        .swiperList"
                                    :key="index"
                                >
                                    <div class="swipr-inner">
                                        <a :href="item.path">
                                            <span>{{ item.tips }}</span>
                                            <div class="img-container">
                                                <img
                                                    :src="item.img_url"
                                                    class="img"
                                                />
                                            </div>
                                        </a>
                                    </div>
                                </van-swipe-item>
                            </van-swipe>
                        </div>
                    </template>
                    <IndexBlogItem
                        class="blogs"
                        :middleBlogList="$store.state.blog.indexBlogs"
                        :handleView="handleView"
                    />
                </div>
            </van-tab>
        </van-tabs>
    </div>
</template>

<script>
export default {
    props: {
        active: {
            type: String
        }
    },
    inheritAttrs: false,
    data() {
        return {};
    },
    mounted() {
        this.$store.dispatch("getIndexPageBlog", {});
        this.setScroll();
    },
    computed: {
        active1() {
            return this.active === ""
                ? this.$store.state.tab.indexTab.reduce((pre, cur) =>
                      pre.isActive ? pre.name : cur.name
                  )
                : this.active;
        }
    },
    methods: {
        handleView(id) {
            this.$router.push("/show-blog/" + id);
        },
        handleSearchTab() {
            this.$router.push("/index?key=IndexNavSearch");
        },
        setTabData(name) {
            this.$router.push("/index?tab=" + name);
            this.$store.dispatch("setIndexTab", name);
        },
        async scroll(e) {
            await this.$store.dispatch("setIndexTabScroll", {
                name: this.active,
                scrollHeight: e.target.scrollTop
            });
        },
        setScroll() {
            this.$nextTick(function() {
                for (const key in this.$refs) {
                    let h = this.$store.state.tab.indexTab.reduce((pre, cur) =>
                        pre.name === key ? pre.oldScroll : cur.oldScroll
                    );
                    this.$refs[key][0].scrollTo(0, h);
                }
            });
        }
    }
};
</script>

<style lang="scss" scoped>
.index-home {
    width: 100%;
    height: 100%;

    .swiper-item {
        width: 100%;
        height: calc(100vh - 148px);
        overflow-y: auto;
        font-size: 18px;
        .index-swiper-container {
            height: 3rem;
            background-image: linear-gradient($uni-color-primary-phone, #fff);
            width: 100%;
            .my-swipe {
                width: 100%;
                height: 100%;

                .swipr-inner {
                    width: 100%;
                    height: 100%;
                    box-sizing: border-box;
                    padding: 0 0.2rem;
                    a {
                        width: 100%;
                        height: 100%;
                        span {
                            position: absolute;
                            left: 0.4rem;
                            z-index: 1;
                            color: #fff;
                            bottom: 5px;
                        }
                        .img-container {
                            width: 100%;
                            height: 100%;
                            position: relative;
                            overflow: hidden;
                            .img {
                                position: absolute;
                                width: 100%;
                                border-radius: $uni-border-radius-base
                                    $uni-border-radius-base 0 0;
                                height: auto;
                                left: 0;
                                top: 0;
                            }
                        }
                    }
                }
            }
        }
    }
    // .hot {
    // }

    // .new {
    // }
    .blogs {
        margin-top: 0.1rem;
        padding: 0 0.2rem;
        background-color: #f5f6f7;
    }
}
</style>