<template>
    <div class="index-search">
        <van-search
            shape="round"
            v-model.trim="value"
            show-action
            background="#4fc08d"
            placeholder="请输入搜索关键词"
            @search="onSearch"
            @cancel="onCancel"
        />
        <div class="blogs-container">
            <transition-group tag="ul" name="fade">
                <li
                    v-for="item in $store.state.blog.homeSearchList"
                    :key="item._id"
                >
                    <MyPageBlogItem :blog="item" />
                </li>
            </transition-group>
            <EmtyTips v-show="!$store.state.blog.homeSearchList.length" />
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            value: "vue"
        };
    },
    methods: {
        onSearch(val) {
            this.$store.dispatch("homeSearch", val.trim());
        },
        onCancel() {
            this.$router.go(-1);
        }
    }
};
</script>

<style lang="scss" scoped>
.index-search {
    width: 100%;
    height: calc(100vh - 50px);
    font-size: 16px;
    .blogs-container {
        width: 100%;
        height: calc(100vh - 104px);
        overflow-y: auto;
    }
}
</style>