import router from './router-h5'
import store from './store'
import Vue from 'vue'
import App from './H5'
import mavonEditor from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import '@/assets/media/h5.scss'
import GlobalFilter from '@/utils/filter/globalFilter'
import GlobalMixIn from '@/utils/mixins/globalMixIn'

import RegisterComponents from '@/utils-h5/registerComponents'

//提示用
import { Dialog } from "vant";
import { Notify } from 'vant';

Vue.prototype.$dialog = Dialog
Vue.prototype.$notify = Notify

//编辑器
Vue.use(mavonEditor)

Vue.use(RegisterComponents)

// Filter
Vue.use(GlobalFilter)

//全局mixin 
Vue.use(GlobalMixIn)

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#h5')

