import Vue from 'vue'
import VueRouter from 'vue-router'

import Index from '@/views-h5/index-nav'




Vue.use(VueRouter)


const routes = [
    {
        path: "/",
        redirect: "/index"
    },
    {
        path: "/show-blog/:id",
        name: "show-blog",
        component: () => import('../views-h5/BlogShow')
    },
    {
        path: "/my-blog",
        name: "my-blog",
        component: () => import('../views-h5/MyBlog')
    },
    {
        path: "/my-like",
        name: "my-like",
        component: () => import('../views-h5/MyLike')
    },
    {
        path: "*",
        component: () => import('@/views/NotFound')
    }
]

const headerNav = [
    {
        path: '/index',
        component: Index,
        value: '首页',
        name: 'index',
    },
    // {
    //     path: '/search',
    //     component: () => import('@/views-h5/index-search'),
    //     value: '首页',
    //     name: 'search'
    // },
    // {
    //     path: '/index',
    //     component: () => import('@/views-h5/index-setting'),
    //     value: '首页',
    //     name: 'index'
    // }
]







const router = new VueRouter({
    routes: [...routes, ...headerNav],
    scrollBehavior(to, from, savePostion) {
        if (savePostion) {
            let { x, y } = savePostion
            if (x || y) {
                return savePostion
            }
        }
        return { x: 0, y: 0 }
    }
})



router.beforeEach((to, from, next) => {
    let isLogin = localStorage.getItem("BLOG_TOKEN") ? true : false;
    if (to.path === "/login" || to.path === '/register' || to.name === "show-blog") {
        next()
    } else if ((to.path === '/my-page' || to.path === '/edit') && !isLogin) {
        next("/login")
    } else {
        next()
    }
})

export default router;

export { headerNav }