<template>
    <div class="index">
        <div class="container">
            <transition name="slide">
                <keep-alive>
                    <component :is="component" :active="active"></component>
                </keep-alive>
            </transition>
        </div>
        <van-tabbar
            v-model="component"
            @change="onChange"
            active-color="#07c160"
            inactive-color="#000"
        >
            <van-tabbar-item
                name="IndexNavHome"
                replace
                to="/index?key=IndexNavHome"
                icon="home-o"
                >首页</van-tabbar-item
            >
            <van-tabbar-item
                name="IndexNavSearch"
                replace
                to="/index?key=IndexNavSearch"
                icon="search"
                >搜索</van-tabbar-item
            >
            <van-tabbar-item
                name="IndexNavSetting"
                replace
                to="/index?key=IndexNavSetting"
                icon="setting-o"
                >设置</van-tabbar-item
            >
        </van-tabbar>
    </div>
</template>

<script>
export default {
    data() {
        return {
            component: "IndexNavHome",
            active: ""
        };
    },
    watch: {
        $route: {
            handler(to, from) {
                if (to) {
                    let { key, tab } = to.query;
                    key
                        ? (this.component = key)
                        : (this.component = "IndexNavHome");

                    tab ? (this.active = tab) : (this.active = "");
                }
            },
            immediate: true
        }
    },
    methods: {}
};
</script>

<style lang="scss" scoped>
.index {
    width: 100%;
    height: 100vh;

    .container {
        overflow: hidden;
        width: 100%;
        height: 100%;
    }
}
</style>