import * as H5components from "@/components-h5";
import * as PcComponents from "@/components";
class RegisterComponents {
    constructor() {

    }

    install(Vue, options) {

        // 注册所有组件
        for (const key in H5components) {
            Vue.component(key, H5components[key])
        }

        for (const key in PcComponents) {
            Vue.component(key, PcComponents[key])
        }
    }
}


export default new RegisterComponents()