<template>
    <div class="index-setting">
        <div class="setting-msg">
            <div class="setting-msg-inner">
                <van-icon @click.stop="show = !show" name="setting" />
                <van-icon
                    @click.stop="
                        $notify({ type: 'warning', message: '敬请期待!' })
                    "
                    name="bell"
                />
            </div>
        </div>
        <div class="login-register-container">
            <div class="inner-card">
                <template v-if="this.isLogin">
                    <div class="is-login-bg">
                        <div class="user-head">
                            <div class="img">
                                <img
                                    :src="
                                        user.avatar
                                            ? user.avatar
                                            : '/public/images/default.png'
                                    "
                                />
                            </div>
                            <div class="user-name">
                                <p class="username">{{ user.name }}</p>
                                <p class="email">{{ user.name }}</p>
                            </div>
                        </div>
                        <div class="user-hot-msg">
                            <!-- 动态 -->
                            <p
                                class="user-dongtai"
                                v-for="(item, index) in userHot"
                                :key="index"
                            >
                                <router-link
                                    class="user-top-nav-link"
                                    :to="item.link"
                                >
                                    <span class="num">{{ item.count }}</span>
                                    <span class="num-name">{{
                                        item.title
                                    }}</span>
                                </router-link>
                            </p>
                        </div>
                    </div>
                </template>
                <template v-else>
                    <p class="login-tips">
                        <span>
                            <a href="/public/pc.html#/login?redirect=1"
                                >立即登录, 玩转博客<van-icon
                                    name="arrow"/><van-icon
                                    name="arrow"/><van-icon name="arrow"
                            /></a>
                        </span>
                    </p>
                    <p class="other-login">
                        <van-button square icon="phone-o">号码登录</van-button>
                        <van-button square icon="chat-o">社交账号</van-button>
                    </p>
                    <p class="register-tips">
                        还没有账号?<span
                            ><a href="/public/pc.html#/register?redirect=1"
                                >快速注册</a
                            ></span
                        >
                    </p>
                </template>
            </div>
        </div>
        <ul class="my-link">
            <li v-for="(item, index) in myLink" :key="index">
                <router-link :to="item.link" class="link-item">
                    <van-icon :name="item.icon" />
                    <span>{{ item.title }}</span>
                </router-link>
            </li>
        </ul>
        <van-action-sheet
            v-model="show"
            :actions="actions"
            @select="onSelect"
            cancel-text="取消"
        />
    </div>
</template>

<script>
export default {
    data() {
        return {
            show: false,
            actions: [{ name: "切换账户", subname: "登录" }],
            userHot: [
                {
                    title: "动态",
                    count: 10,
                    link: "/my-blog"
                },
                {
                    title: "关注",
                    count: 3,
                    link: "/my-like-user"
                },
                {
                    title: "粉丝",
                    count: 0,
                    link: "/my-fans"
                }
            ],
            myLink: [
                {
                    title: "我的关注",
                    icon: "like",
                    link: "/my-focus"
                },
                {
                    title: "我的收藏",
                    icon: "star",
                    link: "/my-like"
                },
                {
                    title: "我的博客",
                    icon: "todo-list",
                    link: "/my-blog"
                },
                {
                    title: "我的关注",
                    icon: "like-o",
                    link: "/my-focus"
                },
                {
                    title: "我的关注",
                    icon: "like-o",
                    link: "/my-focus"
                },
                {
                    title: "我的关注",
                    icon: "like-o",
                    link: "/my-focus"
                },
                {
                    title: "我的关注",
                    icon: "like-o",
                    link: "/my-focus"
                },
                {
                    title: "更多",
                    icon: "weapp-nav",
                    link: "/my-focus"
                }
            ]
        };
    },
    methods: {
        onSelect(item) {
            switch (item.name) {
                case "切换账户":
                    location.href = "/public/pc.html#/login?redirect=1";
                    break;
                default:
                    break;
            }
        }
    }
};
</script>

<style lang="scss" scoped>
.index-setting {
    width: 100%;
    font-size: 16px;
    height: calc(100vh - 50px);
    div.setting-msg {
        width: 100%;
        height: 1.33333rem;
        background-color: $uni-color-primary-phone;
        div.setting-msg-inner {
            width: 2.65rem;
            height: 100%;
            box-sizing: border-box;
            padding: 0.2rem 0.25rem;
            float: right;
            .van-icon {
                box-sizing: border-box;
                padding: 0.25rem;

                width: 1rem;
                height: 1rem;
                font-size: calc(0.65rem / 1.3333);
            }
        }
    }
    div.login-register-container {
        width: 100%;
        height: 5rem;
        background-image: linear-gradient(
            $uni-color-primary-phone,
            $uni-color-primary-phone,
            #fff
        );
        padding: 0.2rem 0.2rem 0;
        overflow: hidden;
        box-sizing: border-box;

        div.inner-card {
            width: 100%;
            height: 4.8rem;
            background-color: #fff;
            border-radius: $uni-border-radius-base;
            text-align: center;
            display: flex;
            flex-direction: column;
            /*已登录提示*/
            .is-login-bg {
                width: 100%;
                height: 100%;
                background-image: linear-gradient(
                    $uni-color-primary-phone,
                    $uni-color-primary-phone,
                    #fff
                );
                div.user-head {
                    box-sizing: border-box;
                    width: 100%;
                    color: #fff;
                    height: 2.4rem;
                    padding: 0.2rem 0.4rem;
                    background-color: $uni-color-primary-phone;
                    display: flex;
                    flex-direction: row;
                    div.img {
                        height: 1.5rem;
                        width: 1.5rem;
                        img {
                            height: 1.5rem;
                            width: 1.5rem;
                            border-radius: $uni-border-radius-circle;
                        }
                    }
                    /*已登录用户名 */
                    div.user-name {
                        overflow: hidden;
                        width: 100%;
                        margin-left: 0.5rem;
                        .username {
                            width: 100%;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            white-space: nowrap;
                            font-weight: bold;
                            font-size: calc(1rem / 1.33333);
                        }
                        .mail {
                            overflow: hidden;
                            text-overflow: ellipsis;
                            font-weight: bold;
                            white-space: nowrap;
                            font-size: calc(1rem / 1.5);
                        }
                        p {
                            height: 50%;
                            text-align: left;
                        }
                    }
                }
                /*已登录动态信息 */
                .user-hot-msg {
                    box-sizing: border-box;
                    width: 100%;
                    height: 2.4rem;
                    background-color: #fff;
                    border-radius: $uni-border-radius-base;
                    display: flex;
                    flex-direction: row;

                    p.user-dongtai {
                        text-align: center;
                        width: 100%;
                        line-height: 2.4rem;
                        .user-top-nav-link {
                            width: 100%;
                            height: 100%;
                            span.num {
                                font-size: calc(1rem / 1.33333);
                            }

                            span.num-name {
                                color: $uni-text-color-grey;
                                margin-left: 2px;
                            }
                        }
                    }
                }
            }
            /*未登录提示*/
            .login-tips {
                height: 1.8rem;
                width: 100%;
                font-size: 16px;
                font-weight: bold;
                box-sizing: border-box;
                vertical-align: middle;
                line-height: 1.8rem;
                padding: 0 25%;
                .van-icon {
                    vertical-align: middle;
                    font-size: 12px;
                    color: $uni-color-primary;
                }
            }
            .other-login {
                box-sizing: border-box;
                padding: 0 0.75rem;
                height: 1.2rem;
                & > button {
                    width: 50%;
                    height: 100%;
                    border-radius: 0 1.2rem 1.2rem 0;
                    background-color: #eaf4fd;
                    color: $uni-color-primary;
                    font-weight: bold;
                    font-size: 14px;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    border: 0;
                    overflow: hidden;
                    &:first-of-type {
                        background-color: #fff1dc;
                        color: $uni-color-warning;
                        border-radius: 1.2rem 0 0 1.2rem;
                    }
                }
            }
            .register-tips {
                height: 1.8rem;
                width: 100%;
                font-size: 16px;
                box-sizing: border-box;
                vertical-align: middle;
                color: $uni-text-color-grey;
                line-height: 1.8rem;
                span {
                    color: $uni-color-primary;
                    font-size: 14px;
                    margin-left: 2px;
                }
            }
        }
    }
    .my-link {
        margin-top: 12px;
        width: 100%;
        background-color: #fff;
        height: 3.4rem;
        display: grid;
        grid-template-columns: repeat(4, 25%);
        grid-template-rows: repeat(2, 50%);
        text-align: center;
        li {
            .link-item {
                width: 100%;
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                .van-icon {
                    font-size: 0.5rem;
                    color: $uni-color-primary-phone;
                }
                span {
                    margin-top: 0.2rem;
                    font-weight: bold;
                    font-size: 14px;
                }
            }
        }
    }
}
</style>